isplay: inline-block;\n      height: 3.125rem;\n      line-height: 3.125rem;\n      background: #2c2c2c;\n      border-radius: 5px;\n      margin: 0.3125rem auto;\n      position: relative;\n}\n.content .right a:first-of-type {\n        width: 45%;\n}\n.content .right a:nth-of-type(2) {\n        width: 35%;\n}\n.content .right a:hover {\n        background: #333;\n}\n.content .right a input[type=\"file\"] {\n        opacity: 0;\n        position: absolute;\n        top: 0;\n        left: 0;\n        height: 0;\n        height: 3.125rem;\n        width: 100%;\n        cursor: pointer;\n}\n.content .right .download-group {\n      float: right;\n      vertical-align: middle;\n      width: 18%;\n      height: 3.125rem;\n      position: relative;\n}\n.content .right .download-group a {\n        position: absolute;\n        top: 0;\n        left: 0;\n        height: 3.125rem;\n        width: 100%;\n}\n.content .right .download-group .unfinish-btn {\n        background: #999;\n}\n.content .right .list {\n      width: 43.75rem;\n}\n.content .left {\n    width: 21.875rem;\n    height: 31.25rem;\n    background: #eee;\n    border-radius: 0.625rem;\n    padding: 0.9375rem;\n    margin: 0.9375rem auto;\n    overflow: hidden;\n}\n.content .left .qrcode-content {\n      height: 100%;\n      background: #2c2c2c;\n      position: relative;\n      background: #eee;\n      margin: 0 auto;\n}\n.content .left .qrcode-content .loading {\n        position: absolute;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        margin: auto;\n        width: 50px;\n        height: 50px;\n}\n.content .left .qrcode-content .loading img {\n          width: 50px;\n          height: 50px;\n}\n.content .left .qrcode-content .qrcode-border {\n        position: absolute;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        margin: auto;\n        display: block;\n        width: 21.875rem;\n        height: auto;\n}\n.content .left .qrcode-content #qrcode {\n        position: absolute;\n        top: 0;\n        left: 0;\n        background-size: 50%;\n}\n"],"sourceRoot":""}]);

